<template>
	<view @click="clickEvent">
		<w-select :defaultValue="defaultValue" :value="value" :width="width"></w-select>
		<u-picker @confirm="confirm" @cancel="close" :show="show" :columns="dataList" :keyName="keyName">
		</u-picker>
	</view>
</template>
<script>
	export default {
		name: "w-picker",
		props: {
			//默认显示的文字
			defaultValue: {
				type: String,
				default: ''
			},
			//绑定的值
			value: {
				default: ''
			},
			//设置宽度
			width: {
				default: '550rpx'
			},
			//隐藏还是显示
			show: {
				type: Boolean,
				default: false
			},
			//数组列表
			list: {
				default: () => []
			},
			//数组绑定的key值，即需要显示的值
			keyName: {
				default: ''
			}
		},
		computed: {
			dataList() {
				const List = []
				List.push(this.list)
				return List
			}
		},
		methods: {
			clickEvent() {
				this.$emit('click')
			},
			confirm(e) {
				console.log(e)
				this.$emit('confirm', e)
			},
			close() {
				this.$emit('close')
			}
		},
	}
</script>
<style lang="scss" scoped>
</style>
